.info {
  font-family: "Noto Sans";

  border-radius: 10px;

  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 15px 25px;
  background-color: #25273c;
  border: 0;
  font-size: 16px;
  color: gray;
  list-style: none;
  border-bottom: 1px solid gray;

  @media (max-width: 576px) {
    position: relative;
  }
}

.infoBtn {
  display: flex;
  gap: 20px;

  @media (max-width: 576px) {
    position: absolute;
    left: 0;
    top: 70px;
    margin: 0 auto;
    width: 100%;
    height: 47px;

    display: flex;
    justify-content: center;

    background-color: #25273c;

    border-radius: 10px;
  }
}

.infoText {
  font-size: 14px;
}

.clear {
  font-family: "Noto Sans";
  font-size: 14px;
  color: gray;
  border: 0;
  background-color: transparent;
  cursor: pointer;

  transition: color 0.2s ease-in;

  &:hover {
    color: rgb(255, 255, 255);
  }
}

.tabs {
  font-size: 14px;

  font-family: "Noto Sans";

  font-weight: 700;

  color: gray;
  border: 0;
  background-color: transparent;
  cursor: pointer;

  transition: color 0.2s ease-in;

  &:hover {
    color: rgb(255, 255, 255);
  }
}

.tabActive {
  color: rgb(96, 154, 221);

  &:hover {
    color: rgb(96, 154, 221);
  }
}
