@font-face {
  font-family: "Noto Sans";
  font-weight: 400;
  font-style: normal;
  src: url('./assets/fonts/NotoSans-Regular.woff2') format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Noto Sans";
  font-weight: 700;
  font-style: normal;
  src: url("./assets/fonts/NotoSans-Bold.woff2") format("woff2");
  font-display: swap;
}

#root {
  position: relative;
}

.App {
  text-align: left;
  max-width: 570px;
  margin: 0 auto;
  padding: 25px 10px;

  @media (max-width: 576px) {
    padding: 25px 10px 120px;
  }
}

.title {
  font-size: 40px;
  letter-spacing: 17px;
  margin-bottom: 38px;

  @media (max-width: 576px) {
    font-size: 30px;
  }
}

.empty {
  font-size: 30px;

  @media (max-width: 576px) {
    font-size: 25px;
  }
}

.container {
  position: absolute;
  background: linear-gradient(
      90deg,
      rgba(0, 22, 255, 0.7) 0%,
      rgba(223, 74, 230, 0.7) 100%
    ),
    url("assets/img/bg.jpg") center/cover no-repeat;
  width: 100%;
  height: 280px;
  top: 0;
  z-index: -1;

  @media (max-width: 576px) {
    height: 240px;
  }
}
