.input {
  font-family: "Noto Sans";
  width: 100%;
  padding: 23px 5px 23px 70px;
  background-color: #25273c;
  border: 0;
  font-size: 18px;
  color: white;
  border-radius: 10px;

  @media (max-width: 576px) {
    font-size: 16px;
    padding: 20px 5px 20px 50px;
  }
}

.input::placeholder {
  font-family: "Noto Sans";
  font-size: 18px;
  color: gray;

  @media (max-width: 576px) {
    font-size: 16px;
  }
}

.form {
  position: relative;
  margin-bottom: 22px;
}

.form::before {
  content: "";
  position: absolute;
  top: 50%;
  translate: 0 -50%;
  left: 25px;
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: 1px solid gray;
  border-radius: 50%;

  @media (max-width: 576px) {
    width: 22px;
    height: 22px;

    left: 15px;
  }
}
