.todolist {
  border-radius: 10px;
  list-style: none;
  background-color: #25273c;
}

.todoitem {
  position: relative;
  width: 100%;
  padding: 20px 5px 20px 70px;
  border: 0;
  font-size: 18px;
  color: white;
  border-bottom: 1px solid gray;
  overflow: hidden;

  @media (max-width: 576px) {
    padding: 10px 5px 10px 50px;
    font-size: 16px;
  }
}

.todoitem.todoitemChecked .btn {
  background: linear-gradient(
    135deg,
    rgba(0, 207, 251, 1) 0%,
    rgba(212, 78, 218, 1) 100%
  );
}

.todoitem.todoitemChecked .btn::after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/icons8-done.svg");
  background-size: cover;
  background-repeat: no-repeat;
  inset: 0;
}

.todoitem.todoitemChecked p {
  text-decoration: line-through;
  color: gray;
}

.btn {
  content: "";
  position: absolute;
  top: 50%;
  translate: 0 -50%;
  left: 25px;
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: 1px solid gray;
  border-radius: 50%;
  cursor: pointer;

  @media (max-width: 576px) {
    width: 22px;
    height: 22px;

    left: 15px;
  }
}
